import React from 'react';
import * as S from '../Error/Error.styles';
import home from '@app/assets/images/home.png';
import { useSelector } from 'react-redux';
import { ScoreModel } from '@app/domain/ScoreModel';

export const ExamResult: React.FC = () => {
  const exam = useSelector((state: Record<string, ScoreModel>) => state.exam);

  return (
    <S.Wrapper>
      <S.Image preview={false} src={home} style={{ maxWidth: 400 }} />
      <S.ContentWrapper>
        <S.Title>Congrats!</S.Title>
        <S.Text>Here is your score: {exam.score}</S.Text>
      </S.ContentWrapper>
    </S.Wrapper>
  );
};
